.search-results {
  padding: 0rem 0;
  display: flex;
  flex-direction: column;

}

.results {
  list-style: none;
  margin-bottom: 2rem;
}

.pagination {
  display: block !important;
  margin-top: auto;


  &::after {
    content: '';
    display: table;
    clear: both;
  }

  &__btn {
    &--prev {

      float: left;
      text-align: left;
    }
    &--next {
   
      float: right;
      text-align: right;
    }
  }
}


